  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #F5F5F5;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
  border-radius: 1rem;
}

h1, h2, h3, h4 {
  font-family: "Calistoga display", serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  color: #000000;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.container-centered {
  max-width: 80rem;
}

.align-center {
  display: flex;
  justify-content: center;
}

.w-100 {
  width: 100%;
}

.btn-yw {
  background-color: #FF6646;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 1rem;
  border: none;
}
.btn-yw:hover {
  background-color: #ff512d;
  cursor: pointer;
}

.btn-yw-block {
  background-color: #FF6646;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 1rem;
  border: none;
}
.btn-yw-block:hover {
  background-color: #ff512d;
  cursor: pointer;
}

.btn-blue {
  background-color: #233442;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 1rem;
  border: none;
}
.btn-blue:hover {
  background-color: #1a2731;
  cursor: pointer;
}

.btn-blue-block {
  background-color: #233442;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 1rem;
  border: none;
}
.btn-blue-block:hover {
  background-color: #1a2731;
  cursor: pointer;
}

.btn-grn-block {
  background-color: #1fc543;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 1rem;
  border: none;
}
.btn-grn-block:hover {
  background-color: #1caf3c;
  cursor: pointer;
}

.btn-red-block {
  background-color: rgb(191, 23, 23);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 1rem;
  border: none;
}
.btn-red-block:hover {
  background-color: #a81414;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode p, .dark-mode a {
  color: #FFFFFF;
}
.dark-mode .property {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .property .price {
  color: #233442;
}
.dark-mode .property .icon {
  filter: invert(100%);
}
.dark-mode .about-text blockquote {
  color: #FFFFFF;
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/* UI */
.no-webp .header-index {
  background-image: url("../img/header2.jpg");
}

.webp .header-index {
  background-image: url("../img/header2.webp");
}

.header {
  border-bottom: 1px solid #e1e1e1;
}
.header.header-index {
  background-position: center center;
  background-size: cover;
  border: none;
}
@media (min-width: 768px) {
  .header.header-index {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.header-index .container-header {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
}
.container-header span {
  color: #FF6646;
}
.container-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 6rem;
}

.header-txt {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 13rem;
}
.header-txt .header-btn {
  max-width: 20rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #EDEDED;
  margin-bottom: 0;
}
.footer .container-footer {
  padding-top: 2.5rem;
}
.footer .navigation {
  justify-content: center;
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .navigation a {
  color: #233442;
}
.footer .copyright {
  color: #233442;
  text-align: center;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}
.bar h1 {
  color: #233442;
}

.bar-index h1 {
  color: #FFFFFF;
}

.navigation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navigation a {
  color: #233442;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
  }
  .navigation alast-of-type {
    margin-right: 0;
  }
}
.navigation a:hover {
  color: #FF6646;
}

.navigation-index a {
  color: #FFFFFF;
}

/* Burger menu */
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.dark-mode-btn {
  width: 2.5rem;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .dark-mode-btn {
    margin: 0;
  }
}
.dark-mode-btn:hover {
  cursor: pointer;
}

.dark-mode-btn-index {
  filter: invert(100%);
}

@media (min-width: 768px) {
  .about-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 7.5rem;
    margin-bottom: 7.5rem;
  }
}
.icon {
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
}
.icon img {
  height: 5rem;
  width: auto;
  margin: auto;
}
.icon h3 {
  color: #FF6646;
}
.icon p {
  line-height: 3rem;
}
.icon a {
  font-weight: 700;
  padding: 1rem 2.5rem;
  border-radius: 1rem;
  width: 20rem;
}
.icon .btn-b-yw {
  border: 1.5px solid #FF6646;
  color: #FF6646;
  margin-top: 2.5rem;
  display: inline-block;
}
.icon .btn-b-yw:hover {
  background-color: #FF6646;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .properties-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.properties-container {
  color: #233442;
}

.property {
  background-color: #eeeeee;
  border: 1px solid #fbfbfb;
  margin-bottom: 2rem;
  border-radius: 1rem;
}
.property .properties-content {
  padding: 2rem;
}
.property h3, .property p {
  margin: 0;
  text-align: left;
}
.property h3 {
  font-weight: 400;
}
.property img {
  border-radius: 1rem 1rem 0 0;
}

.price {
  color: #233442;
  font-weight: 700;
  font-size: 2.5rem;
}

.properties-icons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.properties-icons li {
  flex: 1;
  display: flex;
  align-items: center;
}
.properties-icons li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.properties-icons li p {
  font-weight: 900;
}

.no-webp .img_contact {
  background-image: url("../img/encuentra.jpg");
}

.webp .img_contact {
  background-image: url("../img/encuentra.webp");
}

.img_contact {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img_contact h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: #000000;
}
.img_contact p {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.img_contact .btn-contact {
  width: 20rem;
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .inf-section {
    display: grid;
    column-gap: 2rem;
    grid-template-columns: 2fr 1fr;
  }
  .inf-section h3 {
    text-align: left;
    color: #233442;
  }
}
.blog-entry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}
.blog-entry img {
  border-radius: 1rem;
}

.info-meta {
  font-size: 1.6rem;
  color: #546675;
}
.info-meta span {
  color: #FF6646;
}

.entry-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .entry-text a {
    margin-top: 0;
  }
}
.entry-text h4 {
  margin: 0;
  line-height: 1.4;
  text-align: left;
  font-weight: 700;
  color: #233442;
}
.entry-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.4rem;
  background-color: #233442;
  margin-top: 1rem;
  border-radius: 1rem;
}

.testimonial {
  background-color: #233442;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .form-contact {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/* Internal pages */
@media (min-width: 768px) {
  .container-about {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  margin: 1rem 0;
}
.alert.error {
  background-color: rgb(191, 23, 23);
}
.alert.success {
  background-color: #1fc543;
}

table.properties {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.properties thead {
  background-color: #233442;
}
table.properties thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.properties .table-img {
  width: 15rem;
}

.img-s {
  width: 20rem;
  margin-bottom: 2rem;
}